@import "utils";
@import "fonts";
@import "typography";



html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
}
*{
  box-sizing: border-box;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
	display: block;
}
body {
	line-height: 1;
}
ol, ul {
	list-style: none;
}
blockquote, q {
	quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}
table {
	border-collapse: collapse;
	border-spacing: 0;
}

body, html{
  scroll-behavior: smooth;
}

.show-menu{
  overflow: hidden;
}
//hero-image start
.hero-image{
  position: relative;

  &__background{
    object-fit: cover;
    width: 100%;
    height: 700px;
    display: block;

    @media (max-width: 767px){
      height: 568px;
    }
  }
}
//hero-image end

//header start
.header{
  position: absolute;
  max-width: 1072px;
  width: 100%;
  top: 24px;
  left: 50%;
  padding: 0 30px;
  transform: translate(-50%);
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;

  &-menu{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    height: 16px;
    width: 24px;
    padding: 0;
    border: none;
    font: inherit;
    color: inherit;
    background-color: transparent;
    cursor: pointer;

    @media(max-width: 767px){
      width: 18px;
      height: 12px;
    }

    &__line1, &__line2, &__line3{
      width: 100%;
      background-color: white;
      height: calc(100% / 5);
      transition: all 0.3s;

      @media(max-width: 767px){
        width: 100%;
        background-color: white;
        height: 2px;
        margin-bottom: 2px;
        transition: all 0.3s;
      }
    }
  }

  @media(max-width: 767px){
    &__logo{
      height: 46px;
      width: 79px;
    }
  }

  &-phone-call{
    display: flex;
    flex-wrap: nowrap;

    @media(max-width: 767px){
      width: 18px;
      height: 18px;
    }

    &__number{
      width: 0;
      height: 16px;
      overflow: hidden;
      transition: all 0.3s;
      font-family: 'Roboto';
      font-weight: 700;
      font-size: 12px;
      line-height: 16px;
      display: flex;
      align-items: center;
      letter-spacing: 2px;
      text-transform: uppercase;
      color: #FFFFFF;
      white-space: nowrap;
      align-self: center;
      position: absolute;
      top: 32px;
      right: 48px;
    }
  }
}

@media (min-width: 768px) {
  .header-menu:hover{
    .header-menu__line1{
      width: 17px;
    }
    .header-menu__line3{
      width: 12px;
    }
  }
}

@media (max-width: 767px) {
  .header-menu:hover{
    .header-menu__line1{
      width: 13px;
    }
    .header-menu__line3{
      width: 9px;
    }
  }
}

@media (min-width: 1260px){
  .header-phone-call:hover{
    & .header-phone-call__number {
      width: 151px;
      height: 16px;
      padding-right: 23px;
    }
  }
}
//header end

//hero-title start
.hero-title{
  position: absolute;
  bottom: 80px;
  left: 50%;
  transform: translate(-50%);
  height: 187px;
  max-width: 1072px;
  width: 100%;
  padding: 0 30px;

  @media (max-width: 767px){
    width: 100%;
    height: 107px;
  }

  &__line{
    height: 2px;
    width: 40px;
    background: #FFFFFF;
    margin-bottom: 24px;
  }

  &__text{
    font-family: 'Roboto';
    font-style: normal;
    font-weight: 700;
    font-size: 80px;
    line-height: 80px;
    display: flex;
    align-items: center;
    letter-spacing: -1.5px;
    color: #FFFFFF;

    @media (max-width: 767px){
      font-size: 46px;
      line-height: 40px;
      letter-spacing: -1px;
    }
  }
}
//hero-title end

//menu start
.menu{
  position: fixed;
  top: 0;
  right: 100%;
  width: 100%;
  height: 100%;
  padding: 0 30px;
  // z-index: -1;
  background-color: #0075FF;
  transition: all 0.3s;
  overflow: hidden;

  &-block{
    max-width: 1072px;
    width: 100%;
    top: 25px;
    margin: 0 auto;
    display: flex;
    align-items: center;

    &-close{
      position: absolute;
      top: 54px;
      border: none;
      background-color: transparent;
      cursor: pointer;
      padding: 0;

      @media(max-width: 767px){
        height: 16px;
        width: 16px;
      }

      &__img{
        @media(max-width: 767px){
          height: 16px;
          width: 16px;
        }
      }
    }

    &__logo{
      position: absolute;
      top: 25px;
      left: 50%;
      transform: translate(-50%);

      @media(max-width: 767px){
        height: 46px;
        width: 79px;
      }
    }
  }

  &-links{
    position: absolute;
    top: 152px;
    display: flex;
    flex-direction: column;

    @media(max-width: 767px){
      top: 120px;
    }

    &__item{
      width: fit-content;
      position: relative;
      font-family: Roboto;
      font-size: 22px;
      font-weight: 700;
      line-height: 22px;
      letter-spacing: 2px;
      text-align: left;
      color: #FFFFFF;
      text-transform: uppercase;
      text-decoration: none;
      margin-bottom: 32px;

      &::after{
        content: '';
        position: absolute;
        bottom: -4px;
        left: calc(100%/2);
        width: 0;
        height: 1px;
        background-color: white;
        text-decoration-style: solid;
        transition: all 0.3s;
      }

      &:hover::after{
        width: 100%;
        left: 0;
      }
    }

    &__tel{
      font-size: 12px;
      line-height: 16px;
      margin: 16px 0;


      &:last-child{
        margin-top: 0;
        margin-bottom: 0;

        &::after{
          width: 100%;
          left: 0;
          transition: all 0.3s;
        }
      }

      &:hover::after{
        width: 0;
        left: calc(100%/2);
      }
    }
  }
}

.show-menu .menu{
  right: 0;
}
//menu end

//main start
main{
  background-color: white;
}

.introduction{
  max-width: 1072px;
  width: 100%;
  margin: 0 auto;
  padding: 104px 30px 104px 30px;
  font-family: 'Roboto';
  font-style: normal;
  font-weight: 700;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  background-color: white;

  @media (max-width: 767px) {
      flex-wrap: wrap;
      padding-top: 64px;
      padding-bottom: 64px;
  }


  &__moto{
    max-width: 324px;
    width: 100%;
    font-size: 48px;
    letter-spacing: -1.5px;
    text-align: left;

    @media (max-width: 767px){
      max-width: none;
      font-size: 36px;
      margin-bottom: 48px;
    }

    &-allocation{
      color: #0075ff;
    }
  }

  &-teaser{
    max-width: 498px;
    width: 100%;

    @media (max-width: 767px){
      max-width: none;
    }

    &__title{
      font-family: Roboto;
      font-size: 24px;
      font-weight: 700;
      line-height: 30px;
      color: #0075FF;
      margin-bottom: 16px;
    }

    &__paragraph1, &__paragraph2, &__paragraph3, &__paragraph4{
      font-family: 'Roboto';
      font-style: normal;
      font-weight: 400;
      font-size: 16px;
      line-height: 27px;
      color: #3E3E3E;
      margin-bottom: 25px;
    }

    &__paragraph3, &__paragraph4{
      font-weight: 700;
      letter-spacing: 0px;
      margin-bottom: 0;
    }
  }
}

.section{
  max-width: 1072px;
  width: 100%;
  margin: 0 auto 104px auto;
  padding: 0 30px 0 30px;
  background-color: white;

  @media (max-width: 767px) {
    margin-bottom: 64px;
  }

  &-title{
    font-family: Roboto;
    font-size: 48px;
    font-weight: 700;
    line-height: 48px;
    letter-spacing: -1.5px;
    text-align: center;

    @media (max-width: 767px) {
      font-size: 36px;
      line-height: 36px;
    }

    &::after{
      content: "";
      height: 24px;
      background-image : url("/images/Combined shape.png");
      background-repeat: no-repeat;
      background-position: center;
      display: block;
      left: auto;
      right: auto;
      margin: 5px auto 59px auto;

      @media (max-width: 767px) {
        margin-bottom: 32px;
      }
    }
  }
}

.gallery{
  background-color: white;

  &-photos{
    width: 100%;
    display: flex;
    flex-direction: row;
    gap: 24px;
    margin-bottom: 24px;

    @media (max-width: 767px){
      gap: 20px;
      margin-bottom: 16px;
    }

    &__item{
      width: calc(65% - 12px);
      height: 256px;
      border-radius: 8px;
      object-fit: cover;

      @media (max-width: 767px){
        width: calc(50% - 10px) !important;
        height: 131px;
      }
    }

    &--first{
      & .gallery-photos__item {
        &:last-child {
          width: calc(35% - 12px);
          }
      }
    }

    &--second {
      & .gallery-photos__item {
        width: calc(50% - 12px);
      }
    }

    &--third {
      & .gallery-photos__item {
        &:first-child {
          width: calc(35% - 12px);
        }
      }
    }
  }

  &-description{
    font-family: Roboto;
    font-weight: 700;
    width: 100%;
    font-size: 16px;
    line-height: 27px;
    text-align: left;
    letter-spacing: 1px;
    margin-bottom: 40px;

    @media (max-width: 767px){
      margin-bottom: 32px;
    }

    &__name-building{
      line-height: 27px;
      text-decoration: none;
      color: #0075FF;
      letter-spacing: 1px;
    }

    &__name-building:hover{
      text-decoration: underline;
    }
  }

  &__button-more{
    max-width: 150px;
    width: 100%;
    height: 56px;
    background-color: white;
    border: 2px solid #0075FF;
    border-radius: 8px;
    font-family: 'Roboto';
    font-weight: 700;
    font-size: 16px;
    line-height: 14px;
    color: #0075FF;
    display: block;
    margin: 0 auto;
    transition: all 0.3s;

    @media (max-width: 767px){
      max-width: none;
    }

    &:hover{
      background: #005ECC;
      box-shadow: 0px 13px 27px rgba(0, 83, 181, 0.46);
      color: #FFFFFF;
      transition: all 0.3s;
      border-color: rgba(0, 83, 181, 0.46);
    }

    &:active{
      box-shadow: 0px 8px 14px rgba(0, 83, 181, 0.48);
      border-color: rgba(0, 83, 181, 0.48);
    }
  }
}

.testimonials{
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  gap: 24px;
  text-align: center;
  background-color: white;

  @media (max-width: 767px){
    font-size: 36px;
    line-height: 36px;
    flex-wrap: wrap;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 48px;
  }

  &-item{
    width: 33.3%;

    @media (max-width: 767px){
      width: 100%;
    }

    &__photo{
      display: block;
      margin: 0 auto;
    }

    &__name{
      margin-top: 32px;
      font-family: 'Roboto';
      font-weight: 700;
      font-size: 24px;
      line-height: 30px;
      color: #0075FF;
      letter-spacing: 0.5px;

      @media (max-width: 767px){
        margin-top: 28px;
      }
    }

    &__post{
      font-family: 'Roboto';
      font-weight: 400;
      font-size: 14px;
      line-height: 27px;
      color: #9FADC2;
      letter-spacing: 0.5px;
      margin: 4px 0;
    }

    &__review{
      font-family: 'Roboto';
      font-weight: 400;
      font-size: 16px;
      line-height: 27px;
      color: #3E3E3E;
      letter-spacing: 0.5px;
    }
  }
}

.contacts{
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  padding-bottom: 120px;
  margin-bottom: 359px;
  background-color: white;

  @media (max-width: 767px){
    flex-wrap: wrap;
    margin-bottom: 64px;
  }

  &-col{
    max-width: 411px;
    width: 100%;

    @media (max-width: 767px){
      max-width: initial;;
    }

    &--first{
      display: flex;
      flex-direction: row;
      justify-content: space-between;

      @media (max-width: 767px){
        flex-wrap: wrap;
      }
    }

    &-first, &-second{
      min-width: 150px;
      width: 100%;
    }

    &__label{
      font-family: 'Roboto';
      font-weight: 400;
      font-size: 14px;
      line-height: 27px;
      color: #9FADC2;
      margin-bottom: 8px;
    }

    &__text{
      font-family: 'Roboto';
      font-weight: 400;
      font-size: 16px;
      line-height: 27px;
      color: #3E3E3E;
      margin-bottom: 24px;
    }

    @media (max-width: 767px){
      .contacts-col-second .contacts-col__text{
        margin-bottom: 32px;
      }
    }

    &-form{
      &__item{
        width: 100%;
        height: 48px;
        background: #F7F8FF;
        border-radius: 8px;
        border: 2px solid #F7F8FF;
        font-family: 'Roboto';
        font-weight: 400;
        font-size: 14px;
        line-height: 20px;
        color: #8F99A8;
        margin-bottom: 16px;
        padding: 14px 17px;

        &:last-child{
          margin-bottom: 32px;
          height: 144px;
          text-align: start;
        }

        &:hover{
          border: 2px solid #CCD0E0;
        }

        &:focus {
          border: 2px solid #0075FF;
          color: #000000;
          outline: none;
        }
      }
    }

    &__button{
      width: 100%;
      height: 56px;
      background: #0075FF;
      box-shadow: 0px 6px 23px rgba(0, 117, 255, 0.46);
      border-radius: 8px;
      border: 2px solid #0075FF;
      font-family: 'Roboto';
      font-weight: 700;
      font-size: 16px;
      line-height: 14px;
      color: #FFFFFF;
      display: block;
      margin: 0 auto;
      transition: all 0.3s;

      &:hover{
        background: #005ECC;
        box-shadow: 0px 13px 27px rgba(0, 83, 181, 0.46);
        transition: all 0.3s;
        border-color: rgba(0, 83, 181, 0.46);

      }

      &:active{
        background: #005ECC;
        box-shadow: 0px 8px 14px rgba(0, 83, 181, 0.48);
      }
    }
  }
}
//main end

//footer start
.footer{
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
  z-index: -1;


  &__image{
    width: 100%;
    object-fit: cover;
    height: 359px;
    display: block;

    @media (max-width: 767px){
      height: 206px;
    }
  }

  &__logo{
    position: absolute;
    bottom: 35px;
    left: 50%;
    transform: translate(-50%);

    @media(max-width: 767px){
      height: 46px;
      width: 79px;
      bottom: 26px;
    }
  }
}
//footer end

